@import 'colors';

:local(.about) {

    .links {
        text-align: center;
        margin: 4% auto 0;
        width: 25%;
        color: white;
    }

    .content {
        text-align: center;
        color: white;
        background-color: $dark-greyish-red;
        border-radius: 15px;
        width: 60%;
        height: 400px;
        margin: 5% auto;
        padding: 5%;
    }

    h2 {
        background-color: $army-green;
        color: white;
        border-radius: 15px;
        text-align: center;
        width: 40%;
        margin: 2% auto;
        padding: 2%;
    }

    a {
        text-decoration: none;
        border-radius: 15px;
        background-color: $redish-grey;
        color: white;
        margin: 1%;
        padding: 2% 4%;
    } 

}



