@import 'colors';

:local(.header) {

    h1 {
        background-color: $grey-green;
        border-radius: 15px;
        text-align: center;
        width: 50%;
        margin: 5% auto;
        padding: 2%;
    }

    div {
        text-align: center;
        margin: 0 auto 4%;
        width: 25%;
        color: white;
    }

    a {
        text-decoration: none;
        border-radius: 15px;
        background-color: $redish-grey;
        color: white;
        margin: 1%;
        padding: 2% 4%;
    }

}



