@import 'colors';

:local(.image) {

    h2 {
        background-color: $army-green;
        color: white;
        border-radius: 15px;
        text-align: center;
        width: 40%;
        margin: 2% auto;
        padding: 2%;
    }

    h3 {
        background-color: $redish-grey;
        color: white;
        border-radius: 15px;
        text-align: center;
        width: 30%;
        margin: 2% auto;
        padding: 1%;
    } 

    select {
        display: block;
        margin: 2% auto;
        width: 175px;
        border-radius: 15px;
    }

}





