@import 'colors';

:local(.welcome) {

    div {
        text-align: center;
        color: white;
        background-color: $dark-greyish-red;
        border-radius: 15px;
        width: 60%;
        height: 400px;
        margin: 8% auto 5%;
        padding: 5%;
    }
    
    h2 {
        background-color: $army-green;
        color: white;
        border-radius: 15px;
        text-align: center;
        width: 40%;
        margin: 2% auto;
        padding: 2%;
    }  

}



